.screen-wrapp {
  position: relative;
  padding: 50px 0 200px 0;
  @media(max-width: $container-sm) {
    padding-bottom: 50px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;
    width: 50%;
    height: 100%;
    background: url("../img/bg-left.png") no-repeat top;
    background-size: contain;
    @media(max-width: $container-sm) {
      display: none;
    }
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: -50px;
    width: 50%;
    height: 100%;
    background: url('../img/bg-right.png') no-repeat top;
    background-size: contain;

    @media(max-width: $container-sm) {
      display: none;
    }
  }
}
.screen {
  display: flex;
  overflow: hidden;
  height: 800px;
  position: relative;
  z-index: 3;

  @media(max-width: $container-sm) {
    flex-wrap: wrap;
    height: auto;
  }
  &__banner {
    position: relative;
    width: 40%;
    @media(max-width: $container-sm) {
      width: 100%;
      height: 500px;
      overflow: hidden;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      @media(max-width: $container-sm) {
        //height: auto;
      }
    }
    &::before {
      content: "";
      position: absolute;
      bottom: -160px;
      left: -210px;
      background: url("../img/banner-logo.png") no-repeat;
      width: 482px;
      height: 472px;
      z-index: 2;
      @media(max-width: $container-md) {
        display: none;
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
    }
  }
  &__content {
    width: 60%;
    padding: 120px 50px;
    background: $white;
    @media(max-width: $container-sm) {
      width: 100%;
      padding: 25px;
    }
    p {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 30px;
    }
  }
}

.banner-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 120px 30px;
  z-index: 2;
  @media(max-width: $container-sm) {
    padding: 30px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  &__title {
    color: $white;
    text-align: right;
    font-size: 36px;
    text-transform: uppercase;
    margin-bottom: 50px;
    @media(max-width: $container-xs) {
      font-size: 32px;
    }
    &--inner {
      font-size: 24px;
      @media(max-width: $container-xs) {
        font-size: 20px;
      }
      @media(max-width: $container-mob) {
        font-size: 14px;
      }
    }
  }

  &__desc {
    position: relative;
    padding: 30px 15px 30px 0;
    color: #d7d7d7;
    text-align: right;
    font-size: 18px;
    @media(max-width: $container-mob) {
      font-size: 14px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      background: #d7d7d7;

    }
  }
}
