@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/ubuntu/ubuntulight.woff2') format('woff2'),
  url('../fonts/ubuntu/ubuntulight.woff') format('woff'),
  url('../fonts/ubuntu/ubuntulight.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
